.header {
  position: fixed;
  width: 100%;
  background-color: white;
  z-index: 99;
}

.navigate {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 80px;
  padding-left: 120px;
  border-bottom: 1px solid #d5d5d5;
  padding-bottom: 10px;

  &__btn-create {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 130px;
    border-radius: 30px;
    border: none;
    outline: none;
    box-shadow: 0 5px 17px rgba(0, 0, 0, 0.25), 0 2px 4px rgba(0, 0, 0, 0.22);
    background-color: white;
    margin-right: 50px;
    font-size: 18px;

    &:hover {
      box-shadow: 0 7px 20px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
      transition: .2s;
    }
  }

  &__btn-today {
    border: 1px solid grey;
    border-radius: 3px;
    height: 40px;
    width: 90px;
    background-color: transparent;
    outline: none;
    margin-right: 50px;
    font-weight: 700;

    &:hover {
      background-color: #d5d5d5;
      transition: .2s;
    }
  }
}

.navigate__arows {
  display: flex;
  margin-right: 50px;

  &_right {
    background-image: url("https://img.icons8.com/material-sharp/24/000000/forward.png");
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-position: center;
    background-size: auto;

    &:hover {
      background-color: #D3D3D3;
      transition: .2s;
    }
  }

  &_left {
    background-image: url("https://img.icons8.com/material-sharp/24/000000/forward.png");
    width: 35px;
    height: 35px;
    border-radius: 50%;
    transform: rotate(180deg);
    background-position: center;
    background-size: auto;

    &:hover {
      background-color: #D3D3D3;
      transition: .2s;
    }
  }
}

.navigate__title {
  font-size: 24px;
  font-weight: 500;
}